  .underline::after {
    content: '';
    position: absolute;
    bottom: -5px;
    left: -.5rem;
    right: -1.5rem;
    height: 20px;
    z-index: -1;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url("../../dist/img/v2/brushstroke.svg"); }

.underline2 {
  position: relative;
  z-index: 1; }
  .underline2::after {
    content: '';
    bottom: -2.5rem;
    height: 4rem;
    left: -0.5rem;
    right: -1.6rem;
