  .rs-page{
    .rs-head-container{
      background-color: var(--global-color-dark-violet);
      
      .rs-headContent{
        padding-top: 90px;
        padding-bottom: 40px;
        display: flex;
        flex-direction: column;
        align-items: center;
        
        
        &-title{
          max-width: 700px;
          display: flex;
          flex-direction: column;
          align-items: center;
          
          h4{
            font-family: var(--global-font-sectra);
            font-size: 45px;
            color: #FFF;
            font-style: normal;
            font-weight: 700;
            line-height: 52px;
            letter-spacing: 0em;
            text-align: center;
          }
          
          &-copy{
            margin-top:0;
            font-family: var(--global-font-abc-whyte);
            font-size: 21px;
            color: #FFF;
            font-style: normal;
            font-weight: 400;
            line-height: 29px;
            letter-spacing: 0em;
            text-align: center;
          }
          
        }
        
        
        .rs-title {
          margin-top:80px;
          max-width: 1060px;
          display: table;
          white-space: nowrap;
          font-family: var(--global-font-sectra);
          font-size: 35px;
          font-style: normal;
          font-weight: 700;
          line-height: 42px;
          letter-spacing: 0em;
          text-align: left;
          color: #FFF;
          overflow: hidden;
          @media (max-width: $tablet-landscape) {
            padding: 0 2rem;
          }
          
          &:after {
            border-top: 2px solid var(--global-color-light-violet);
            content: '';
            display: table-cell;
            position: relative;
            top: 0.55em;
            width: 100%;
          }
          
          &:after {
            left: 1.5%;
          }
        }
        
        .rs-head-form{
          display: flex;
          flex-direction: column;
          justify-content: flex-start;
          align-items: flex-start;
          max-width: 1060px;
          width: 100%;
          
          @media (max-width: $tablet-landscape) {
            display: none;
          }
          
          .filter-by{
            font-family: var(--global-font-abc-whyte);
            font-size: 11px;
            font-style: normal;
            font-weight: 400;
            line-height: 21px;
            color: #FFF;
            letter-spacing: 0.17em;
            text-align: left;
            margin-bottom: 22px;
            margin-top: 33px;
            @media (max-width: $tablet-landscape) {
              width: 80%;
              margin: 0 auto;
              margin-bottom: 13px;
            }
            
          }
          
          .rs-filters{
            display: flex;
            flex-direction: row;
            color: #FFF;
            justify-content: flex-start;
            width: 100%;
            @media (max-width: $tablet-landscape) {
              flex-direction: column;
              text-align: center;
            }
            
            
            
          }
          
          .dropMenu{
            position: relative;
            margin-right: 28px;
            @media (max-width: $tablet-landscape) {
              margin: 0 auto;
              width:80%;
              margin-bottom: 20px;
            }
            
            .slabel{
              color: white;
              font-size: 11px;
              font-style: normal;
              font-weight: 400;
              line-height: 14px;
              letter-spacing: 0em;
              text-align: left;
              margin-bottom: 13px;
              font-family: var(--global-font-abc-whyte);
            }
            
            ul.dropdown-menu{
              width: 100%;
              background-color: #2E1D97;
              position: absolute;
              display: none;
              list-style: none;
              border-top: 2px solid #3E2BB1;
              z-index: 100;
              
              @media (max-width: $tablet-landscape) {
                text-align: left;
                width: 100%;
              }
              
              li.checkFilter{
                padding: 11px 19px;
                
                label {
                  font-size: 17px;
                  font-style: normal;
                  font-weight: 400;
                  line-height: 24px;
                  letter-spacing: 0em;
                  text-align: left;
                  padding-left: 36px;
                  display: block;
                  transition: 0.1s all;
                  margin: 0;
                  cursor: pointer;
                  background-position: left center;
