@keyframes test-text {
  0% {
    -webkit-transform: translateX(30px);
    transform: translateY(30px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(0);
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes test-text2 {
  0% {
    -webkit-transform: translateX(30px);
    transform: translateY(30px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(0);
    transform: translateY(0);
    opacity: 1;
  }
}