.part_anchor {
  display: block;
  position: relative;
  top: -$header-height * 2;
  visibility: hidden;
}

.icon-share {
  svg {
    fill: $white;
    transition: stroke 1s, fill 1s;
  }

  &:hover,
  &:focus {
    svg {
      fill: $red;
    }
  }
}

body.article-theme--white,
body.article-theme--gray {
  .header-minimal .icon-share {
    svg {
      fill: $black;
    }

    &:hover,
    &:focus {
      svg {
        fill: $red;
      }
    }
  }
}

body.special-report-campaign {
  .header-tiles {
    @include header-alt-style;
    background-color: $black;

    .header__menu-toggle:before {
      //border-bottom-color: #fff;
    }

    > .container {
      height: 100%;
      flex-flow: row nowrap;
    }

    .col-outer {
      flex-flow: row nowrap;
      align-items: center;
    }

    h1 {
      // logo outer
      line-height: 0.5;
    }

    .logo,
    &.mainnav-open .logo {
      @include transition(none);
      display: inline-block;
      width: 22px;
      height: 23px;
      background-image: url(../img/logo_v.svg);
      margin-right: $padding-sm;

      @media (min-width: $tablet-portrait) {
        width: 67px;
        background-image: url(../img/logo_full.svg);
      }
    }

    .header__breadcrumb {
      display: none;
      @extend .ff-sans--bold;
      @include font-size(1.6);
      height: 1.25em; // hmm
      overflow: hidden;
      white-space: nowrap;

      a {
        color: $white;
      }

      @media (min-width: $tablet-portrait) {
        display: inline-block;
        white-space: normal;
      }
    }

    .header__menu-toggle {
      color: #fff;
    }
    @media (min-width: $mobile-large) {
      background-color: transparent;
    }
  }
}

