//     @include big-letter;
//     float: left;
//     margin-right: 5px;
// }

// stream

.stream-module-outer--special-reports {
  background: $med-gray-bg;
  text-align: center;
  padding: $padding-med * 2 0 $padding-med * 3;

  @media (min-width: $tablet-landscape) {
    padding: $padding-lg * 2 0 $padding-lg * 3;
  }

  h2 {
    @extend .ff-serif--medium;
    @include red-line-after;
    padding-bottom: $padding-med;
    margin-bottom: $padding-med;
  }

  .post-module__subheading {
    @extend .ff-serif--italic;
    margin-bottom: $padding-sm;

    @media (min-width: $desktop-mlarge) {
      @include font-size(2.4);
      margin-bottom: $padding-lg;
    }
  }
}

// special reports - single / article

.special-reports__post-header {
  text-align: center;
  margin: $padding-lg;
  padding: $padding-lg $padding-sm;

  &:not(.additional-reading) {
    // @include fancy-corners;
    padding: 0;
    @extend .blockquote--fancy__outer;
  }

  &.additional-reading {
    margin: $padding-sm;
    padding: $padding-sm $padding-xxs;

    & + .post-article {
      padding-top: 0;
    }
  }

  @media (min-width: $tablet-landscape) {
    margin: $padding-lg auto;
    padding: $padding-med * 2;
    @include style-by-columns("width", 8);

    &.additional-reading {
      margin: $padding-sm auto;
      padding: $padding-med;
    }
  }

  @media (min-width: $desktop-mlarge) {
    margin: $padding-med * 2 auto;
    padding: $padding-lg * 2;
    @include style-by-columns("width", 7);
  }

  h1 {
    @extend .h1--alt;
    margin-bottom: $padding-sm;

    @media (min-width: $tablet-landscape) {
      //margin-bottom: $padding-lg;
    }
  }

  &.additional-reading {
    h1 {
      @include font-size(4);
    }
  }
}

.special-reports__post-header {
  @include fancy-corners;
}
.special-reports__post-header-inner {
  @include fancy-corners-inner;
  padding: $padding-lg;
  //@extend .blockquote--fancy__inner;
}

.special-reports__subheading {
  @extend .ff-serif--italic;
  @extend .size--h3;
}

.special-reports__post-byline {
  margin-top: $padding-lg;
  color: $dk-gray;
  @extend .ff-sans--oblique;
  @extend .size--h4;
}

.special-reports__post-date {
  @extend .ff-sans;
  margin: $padding-lg 0;
}

.special-reports__post-share {
  margin-top: $padding-lg;
}

//Copy + Related

.module__related {
  .title {
    @include font-size(2);
  }

  .stream_image {
    width: 100px;
    height: 100px;
    display: block;
    float: left;
    margin-right: $padding-sm;
  }

  .bottom {
    clear: left;
    display: flex;
    align-items: flex-start;
    align-self: flex-end;
  }

  .people-list li {
    margin-left: 0 !important;

    .text-outer {
      .person-name {
        margin-bottom: 0;
        @extend .ff-sans;
        font-weight: bold;
        @include font-size(1.2);
      }
    }
  }

  .stream-module__meta__date {
    @include font-size(1.2);
    margin: $padding-sm $padding-med $padding-med auto;
    display: flex;
    align-items: flex-end;
    line-height: 2.25;
  }

  & + .module__related {
    margin-top: $padding-lg;
  }
}

// footer / footnotes

.special-reports-footer {
  background: $lt-gray;
  padding: $padding-med * 2 0;
  margin-top: $padding-lg;

  & + .post-module {
    margin-top: $padding-lg;
  }

  & + .special-reports-footer {
    margin-top: 0;
    padding-top: $padding-med;
  }

  @media (min-width: $tablet-landscape) {
    padding: $padding-lg * 2 0;
  }

  .module-text {
    padding: 0;
  }

  .rte {
    @include font-size(1.6);
    @extend .ff-serif--italic;
    margin: auto;
    padding-bottom: $padding-lg;
    @include red-line-after;
  }

  b,
  strong {
    @extend .ff-serif--medium;
    font-style: italic;
  }

  &:last-child {
    @include hairline;
  }

  &.event-series {
    margin-bottom: 0;
  }
}

// footer article nav

.special-reports-article-nav {
  background: $black;
  padding: $padding-sm 0;

  @media (min-width: $tablet-portrait) {
    position: relative;

    &:after {
      content: "";
      display: block;
      position: absolute;
      top: $padding-sm;
      bottom: $padding-sm;
      left: 50%;
      border-right: 1px solid $med-gray;
    }
  }

  .col-outer {
    display: block;

    @media (min-width: $tablet-portrait) {
      display: flex;
      align-items: center;
    }
  }

  &.one-empty {
    .article-nav:first-child:before {
      display: none;
    }
  }

  .article-nav {
    @extend .ff-sans;
    @include font-size(1.6);
    display: table;
    width: 100%;
    position: relative;

    > * {
      display: table-cell;
      vertical-align: middle;
    }

    @media only screen and (max-width: $tablet-portrait) {
      &.empty {
        display: none;
      }
    }

    &:not(.empty) {
      @include arrow-after;
    }

    padding: $padding-xxs $padding-lg;

    &:not(.empty):after {
      // arrow-after overrides
      top: 50%;
      margin-top: -5px;
    }

    &.article-prev {
      &:not(.empty):after {
        // arrow-after overrides
        right: inherit;
        left: $padding-sm;
        border: 2px solid $med-gray;
        border-right: 0;
        border-top: 0;
        @include transition((border 0.15s ease-in, left 0.15s ease-in));
      }

      .no-touch &:hover:after {
        right: inherit;
        left: $padding-xxs;
      }

      .img-outer {
        margin-right: $padding-sm;
      }
    }

    &.article-next {
      .img-outer {
        margin-left: $padding-sm;
      }
    }

    &:first-child {
      margin-bottom: $padding-sm;

      &:before {
        content: "";
        display: block;
        position: absolute;
        width: 114px;
        top: 100%;
        left: 50%;
        right: 0;
        border-bottom: 1px solid $med-gray;
        margin-top: $padding-xxs;
        margin-left: -57px;
      }

      @media (min-width: $tablet-portrait) {
        margin: 0;

        &:before {
          content: none;
        }
      }
    }

    @media (min-width: $tablet-portrait) {
      @include font-size(1.8);
      @include width-by-columns(6);

      &:after {
        // arrow-after overrides
        top: 50%;
        margin-top: -11px;
        width: $padding-sm;
        height: $padding-sm;
      }

      &.article-prev {
        text-align: left;
      }

      &.article-next {
        text-align: right;
      }
    }

    @media (min-width: $tablet-landscape) {
      &.article-prev {
        padding-right: $padding-lg;
      }

      &.article-next {
        padding-left: $padding-lg;
      }
    }

    @media (min-width: $desktop-mlarge) {
      &.article-prev {
        padding-right: $padding-lg * 2;
      }

      &.article-next {
        padding-left: $padding-lg * 2;
      }
    }
  }

  .img-outer {
    width: 80px;
    height: 80px;
    @include border-radius(100%);
    overflow: hidden;
  }

  .nav-img-outer {
    display: none;
    width: 100px;

    @media (min-width: $tablet-portrait) {
      display: table-cell;
    }
  }

  a {
    color: $white;
  }
}

p:last-child {
  margin-bottom: 0;
}

.content-error {
  background-color: green;
  color: yellow;
  padding: 1em;
}

/* Shifting Trends Modules */
.bg--black {
  background-color: $black;
  color: $white;
}

.intro-phrases {
  min-height: 100vh;
  position: relative;
  padding-top: 50vh;
  padding-bottom: 80vh;

  @media (max-width: $tablet-portrait) {
    padding-bottom: 20vh;
  }

  @media print {
    padding-top: 2em;
    padding-bottom: 2em;
    min-height: 0;
  }

  @extend .ff-serif--medium;
  @extend .size--h3--large;

  .phrase {
    max-width: 20em;
    @include will-fade-in;

    & + .phrase {
      margin-top: 30vh;
      @media print {
        margin-top: 2em;
      }
    }

    @media print {
      visibility: visible !important;
      opacity: 1 !important;
    }

    &.right {
      margin-left: auto;
    }
  }
}

.hero-statement {
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: cover;
  background-position: center center;

  //override article-hero styles hack
  position: relative !important;
  opacity: 1 !important;
  visibility: visible !important;

  padding: 6rem 3rem;
  min-height: 100vmin;

  @media print {
    min-height: 0;
  }

  &.article-hero .screen {
    background-color: rgba(0, 0, 0, 0.65);
    opacity: 1;

    &.full-opacity {
      background-color: $lt-gray-bg;
    }
  }

  .inner {
    position: relative;
    z-index: 1;
    text-align: center;
    @extend %h1;
    font-family: $ff-gt-sectra;
    max-width: 16em;
    padding-top: $padding-med;
    padding-bottom: $padding-med;
  }

  /*
	@media (min-device-width: $animationScreenMin) {
		.inner {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%,-50%);
			width: 100%;
			text-align: center;
			@extend %h1;
			font-family: $ff-gt-sectra;
			max-width: 16em;

		}
	}
*/
}

.secondary-statement {
  @extend .ff-serif--medium;
  @extend .size--h3--large;
  display: flex;
  align-items: center;
  justify-content: center;

  padding-top: 10rem;
  padding-bottom: 10rem;

  @media (min-width: $desktop-mlarge) {
    padding-top: 14rem;
    padding-bottom: 14rem;
  }

  .inner {
    max-width: 20em;
    text-align: center;
  }

  a {
    color: inherit;
    text-decoration: underline;
    text-decoration-color: $red;

    &:hover {
      color: $red;
    }
  }
}

/*. "articleBuilder" */
$line-thickness: 10px;
$article-layout-breakpoint: $tablet-landscape;
$article-play-button-size: 5rem;

@mixin article-block-margin {
  &:not(:first-child) {
    padding-top: 6rem;
  }
  margin-bottom: 3rem;

  @media (min-width: $desktop-mlarge) {
    margin-bottom: 8rem;
  }
}

.article-body {
  @extend .size--body;
  line-height: 1.5;

  a {
    color: inherit;
  }

  ol,
  ul {
    padding-left: 1em;

    li {
      margin: 0.25em 0;
    }
  }

  blockquote {
    @extend .blockquote--standard;
    color: inherit;
    opacity: 0.8;
  }

  hr {
    border: 0;
    @extend .red-line-after;
  }

  &.has-liststyle {
    ol,
    ul {
      padding-left: 0;
      margin-left: 0;

      li strong {
        display: block;
        padding-bottom: 1em;
      }
    }
  }
}

body {
  transition: background-color 1s, color 1s;
}

body.article-theme--black {
  background-color: $black;
  color: $white;

  .header.header-minimal .header__menu-toggle {
    color: $white;
  }
}

body.article-theme--gray {
  background-color: $lt-gray;
  color: $black;

  .header.header-minimal .header__menu-toggle {
    color: $black;
  }
}

