body.article-theme--gradient--darkgray {
  background-color: #232836;
  .article-theme--gradient--darkgray {
    background-image: linear-gradient(to bottom, #586170, #232836);
  }
  color: $white;

  .header.header-minimal .header__menu-toggle {
    color: $white;
  }
}

.banner-spacer {
  height: 100vh;
}

.screen {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: $black;
  opacity: 0.25;
  &.full-opacity {
    opacity: 1;
  }
}

.article-block-wrapper {
  &.fullscreen {
    width: 100%;
    height: 100vh;
  }

  .fullscreen-inner {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    visibility: hidden;
    z-index: $zNav + 1;

    .article-block-standard {
      margin: 0;
    }
  }
}

.article-hero {
  background-size: cover;
  background-position: center center;
  visibility: hidden;
  opacity: 0;
  padding: 0;

  &,
  &.scaler-container {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1003;
  }

  .scaler {
    z-index: -1;
  }

  @media (min-width: $tablet-landscape) {
    min-height: 100vh;
  }

  .screen,
  .title_wrap {
    z-index: 1;
  }

  .title_wrap {
    position: absolute;
    bottom: 0;
    width: 100%;
    text-align: center;
    padding: 0 1em;

    color: $white;

    @include will-fade-in;
    @include will-slide-up;

    &.long .subtitle {
      @include font-size(2.2);
    }
  }

  .title,
  .subtitle,
  .next-slide {
    margin: 50px auto;
  }

  .next-slide {
    display: block;
  }

  .title {
    @extend .ff-sans;
    @extend .header--xl;
  }
}

.article {
  // &:not(:first-child) > .container {
  // 	padding-top: 4rem;
  // 	padding-bottom: 4rem;

  // 	@media (min-width: $tablet-portrait) {
  // 		padding-top: 8rem;
  // 		padding-bottom: 8rem;
  // 	}

  // 	@media (min-width: $desktop-mlarge) {
  // 		padding-top: 12rem;
  // 		padding-bottom: 12rem;
  // 	}
  // }

  &.container .container {
    padding-left: 0;
    padding-right: 0;

    @media (min-width: $tablet-portrait) {
      padding-left: inherit;
      padding-right: inherit;
    }
  }

  &.container .container.article-block-standard.has-sidebar {
    padding-left: 0;
    padding-right: 0;
  }

  .heading-wrap {
    display: flex;
  }

  .heading-photo {
    width: 100px;
    height: 100px;
    margin-right: 2em;
  }

  .heading-label {
    position: relative;

    > div {
      @extend .ff-sans;
      font-weight: bold;
      @include font-size(1.4);
      text-transform: uppercase;
    }
  }

  .heading-wrap.heading-style--default {
    .heading-wrap-text h3.heading {
      @extend h1;
    }
  }

  .heading-wrap.heading-style--redStripe {
    position: relative;
    align-items: center;
    display: flex;

    .heading {
      display: flex;
      align-items: center;
      position: relative;

      .container {
        width: 100%;
        position: relative;
      }

      @media (min-width: $tablet-landscape) {
        @include font-size(3.4);
      }

      span {
        margin-left: -1em;
        padding: 0 1em;
      }
    }
    &:after {
      //bottom: .75em;
      content: "";
      width: 100%;
      height: 1.4rem;
      background-color: $red;

      @media (min-width: $tablet-portrait) {
        //bottom: 1em;
        height: 1.9rem;
      }

      @media (min-width: $tablet-landscape) {
        //bottom: .6em;
        height: 2.6rem;
      }
    }
  }

  .heading-wrap.heading-style--numberedWithShape {
    position: relative;

    .heading {
      display: flex;
      position: relative;
      @include font-size(3);

      .receives-background-color {
        background-color: transparent;
      }

      .container {
        width: 100%;
        position: relative;
      }

      @media (min-width: $tablet-landscape) {
        @include font-size(3.4);
      }
    }

    .heading-label {
      left: 0;
      margin-bottom: 30px;
      position: relative;
      top: unset;

      > div {
        @extend .ff-sans;
        font-weight: bold;
        text-transform: uppercase;
        @include font-size(1);
      }

      // @media (min-width: $tablet-portrait) {
      //     left: -7%;
      // }

      @media (min-width: $tablet-landscape) {
        left: -10%;
        position: absolute;
        margin-bottom: 0;
        top: 0;
        > div {
          @include font-size(1.3);
        }
      }
    }

    .heading-index {
      @extend .ff-sans--thin;
      line-height: 60px;
      position: relative;
      z-index: 1;
      @include font-size(6);

      @media (min-width: $tablet-landscape) {
        line-height: 80px;
        @include font-size(8);
      }
    }

    .heading-shape {
      display: block;
      height: 60px;
      // left: unset;
      margin: 0 1.5rem;
      position: absolute;
      right: unset;
      top: 30%;
      transform: rotate(90deg);
      width: 31px;
      z-index: 0;

      &:after {
        border-left: 30px solid transparent;
        border-right: 30px solid transparent;
        border-top: 60px solid #000;
        bottom: 0;
        content: "";
        position: absolute;
        right: 0;
      }

      &:before {
        border-bottom: 60px solid #000;
        border-left: 30px solid transparent;
        border-right: 30px solid transparent;
        content: "";
        left: 0;
        position: absolute;
        top: 0;
      }

      @media (min-width: $tablet-landscape) {
        height: 80px;
        left: 0;
        margin: auto;
        right: 0;
        width: 41px;

        &:after {
          border-left: 40px solid transparent;
          border-right: 40px solid transparent;
          border-top: 80px solid #000;
        }
        &:before {
          border-bottom: 80px solid #000;
          border-left: 40px solid transparent;
          border-right: 40px solid transparent;
        }
      }
    }
  }

  h1.heading {
    @extend .ff-sans--bold;
    @extend .header--lg;
  }

  .heading {
    line-height: 1.25;
  }
}

