}

.special-reports-chapter .featured-banner.campaignSlide .title_wrap .subtitle {
  display: none !important;
}
.special-reports-chapter
  .featured-banner.campaignSlide.featured-banner--carousel
  .title_wrap
  .subtitle {
  display: block !important;
}

.no-pad.special-report-campaign .homepage-banner {
  @media (min-width: $mobile-large) {
    margin-top: 0;
  }
}

.featured-banner--slideshow {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  .title {
    padding: 40px;
  }
}

.special-reports-chapter .featured-banner.campaignSlide.hasBgImage {
  .bg-image__caption {
    bottom: 20px;
    left: 0;
    right: calc(50% + 20px);
    position: absolute;
    z-index: 2;

    p {
      font-family: $ff-gt-walsheim;
      max-width: 1400px;
      opacity: 0;
      padding: 20px;
      pointer-events: none;
      transform: translateY(50%);
      transition: all 0.3s;
    }

    @media (max-width: $tablet-landscape) {
      bottom: auto;
      top: 0;
      right: 0;

      .caption-reveal {
        height: 20px;
        padding: 0;
        top: 20px;
        transform: none;
        width: 20px;
      }

      p {
        background-color: rgba(0, 0, 0, 0.75);
      }
    }
  }

  .caption-reveal {
    background-color: transparent;
    border-radius: 50%;
    border: 1px solid #ffffff;
    bottom: 20px;
    color: #ffffff;
    height: 40px;
    left: 20px;
    opacity: 1;
    position: absolute;
    transition: opacity 0.3s;
    width: 40px;
    z-index: 3;

    &:hover {
      cursor: pointer;
      opacity: 0;

      + .bg-image__caption p {
        opacity: 1;
        transform: translateY(0);
      }
    }
  }

  @media (min-width: $tablet-landscape) {
    .caption-reveal:hover {
      // background-color: lime;
      + .bg-image__caption + .screen + .title_wrap {
        opacity: 0;
      }
    }
  }

  @media (max-width: $tablet-landscape) {
    .caption-reveal {
      height: 20px;
      padding: 0;
      top: 20px;
      transform: none;
      width: 20px;
    }
  }
}

.hero-banner {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;

  .hero-banner-title-wrap {
    text-align: center;
    font-size: 1.5em;
    position: relative;
  }
  .subtitle {
    margin-top: 1em;
    @extend .ff-sans;
  }
}

.hero-banner-title-wrap--bars {
  max-width: 25em;

  .title {
    $border-width: 10px;
    border-top: $border-width solid;
    border-bottom: $border-width solid;
    @extend .ff-sans;
    font-weight: bold;
    text-transform: uppercase;
  }
}

// This is to bring content to the top of the page... CMS toggle 'acts as hero'
.article {
  display: flex;
  flex-direction: column;

  .hero-top {
    order: -1;
  }
}

