.item-carousel {
  margin: 2em auto;
  max-width: 80rem;
  padding: 30px 0;

  ul {
    list-style: none;
    margin-bottom: 25px;
    min-height: 150px;
    padding: 2em;

    @media (max-width: $tablet-portrait) {
      min-height: 100px;
    }
  }

  .item-carousel--half {
    display: flex;
    margin-bottom: 0;

    @media (max-width: $tablet-portrait) {
      flex-direction: column;
      .item-carousel__block {
        padding: 1em 0;
      }
    }
  }

  .item-carousel--full {
    margin-bottom: 0;
    .item-carousel__block {
      width: 100%;
    }
  }

  .flickity-prev-next-button {
    background-color: transparent;
  }

  .flickity-page-dots {
    margin-left: -2em;
  }
}

.item-carousel__block {
  min-height: 150px;
  transition: color 0.15s;
  width: 50%;

  @media (max-width: $tablet-portrait) {
    align-items: center;
    min-height: 100px;
    width: 100%;
  }

  &:hover {
    cursor: pointer;
    color: $red;
  }
}

.item-carousel__wrap {
  display: flex;
  height: 100%;
  justify-content: center;

  @media (max-width: $tablet-portrait) {
    align-items: center;
  }
}

.item-carousel__image {
  align-items: center;
  border-radius: 50%;
  display: flex;
  height: 150px;
  justify-content: center;
  max-width: 150px;
  overflow: hidden;
  width: 60%;

  @media (max-width: $tablet-portrait) {
    height: 100px;
    max-width: 100px;
  }

  img {
    display: block;
    height: 100%;
    max-width: none;
    width: auto;
  }
}

.item-carousel__copy {
  align-items: center;
  display: flex;
  @extend .ff-sans;
  margin-left: 20px;
  max-width: 200px;
  width: 40%;
}

.item-carousel__heading {
  text-align: center;
  text-transform: uppercase;
}

.tableauPlaceholder {
  width: 100% !important;
}

.FullColumnImage {
  margin-top: $padding-xl;
  margin-bottom: $padding-xl;
}
